        viewFilecreate:"",
        viewFileurl:"",
        viewFileDuration:'',
        viewFileThumbnail:'',
        modalContent: "",
        selectFile:undefined,
        schoolSession:schoolSession,
        session: storageManager.get('session'),
        configs: storageManager.get('configs'),
        limitOptions: [1,10, 50, 100, 200, 500, 1000, 2000, 3000, 4000], // Define the available options
        totalPages: 0,
        totalItems: 0,

        dragging: false,
        draggedItem: "",
        dropTarget: "",
        selectItem:'',
        selectItemData:'',
        hasSelected:false,

        fileListing:[],
        summaryFileSize:0,
        space:5000,

        capturedThumbnail: null,

        limitItem:50,
        currentPage: 1,
        debounceTimer: null,
        error:false,
        errorText:'',

        // Image Resize Option
        isResizeModalOpen: false,
        selectedFileType: 'image/webp', // Default file type
        selectedImageQuality: 0.8,           // Default quality
        selectedQuality: null, // 
        showConvertOptions: false, // 
        dropdownOpen: false,
        selectedResizeItem:'',
        selectedResizeId:'',
        replaceExistingFile:false,
        isModalVisible: false,
        fileName: '',
        fileUrl: '',

        isBatchResizing: false,
        batchProgress: 0,
        totalBatchFiles: 0,
        processedFiles: 0,

        // Sidebar layout properties
        fileTypeFilter: 'all',
        showMobileSidebar: false,
      }
    },
    components: {
      CreateFolder,
      RenameFolder,
      VideoPlayer
    },
    methods: {
      toggleDropdown() {
        this.dropdownOpen = !this.dropdownOpen;
      },
      selectQuality(quality) {
        this.selectedQuality = quality;
        this.dropdownOpen = false;
      },
      toggleStreamMenu() {
        this.showStreamMenu = !this.showStreamMenu;
      },
      openModal() {
        this.isModalVisible = true;
        this.fileName = '';
        this.fileUrl = '';
      },
      closeModal() {
        this.isModalVisible = false;
        this.fileName = '';
        this.fileUrl = '';
        this.stopVideoPlayback();
      },
      async submitFile() {
        if (this.fileName && this.fileUrl) {
          const file = {
            name: this.fileName,
            original: this.fileUrl,
            path: this.fileUrl,
            size: 0,
            mimetype: 'media',
            type: 'application/octet-stream',
          };

          try {
            await this.addFile(file); // Call the addFile method
            await this.listFile();
            this.closeModal();
          } catch (error) {
            alert('Failed to add streaming file. Please try again.');
            console.error(error);
          }
        } else {
          alert('Please fill in all fields.');
        }
      },
      toggleLayout() {
        this.layout = this.layout === 'grid' ? 'list' : 'grid';
      },
      formatDuration(duration) {
        const totalSeconds = parseFloat(duration);
        if (isNaN(totalSeconds) || totalSeconds <= 0) {
          return '00:00';
        }

        const minutes = Math.floor(totalSeconds / 60);
        const seconds = Math.floor(totalSeconds % 60);

        const formattedMinutes = minutes.toString().padStart(2, '0');
        const formattedSeconds = seconds.toString().padStart(2, '0');

        return `${formattedMinutes}:${formattedSeconds}`;
      },
      handleDragStart(itemTitle) {
        this.dragging = true;
        this.draggedItem = itemTitle;
        debug.log("Dragging started:", this.draggedItem);
      },
      handleDragEnd() {
        this.dragging = false;
        this.draggedItem = "";
      },
      getFileType(mimetype) {
        return convertUtils.getFileType(mimetype, "short");
      },
      getNewFileExtension(fileName) {
        // Logic to extract and return the file extension
        // For example, you can split the fileName by '.' and return the last part
        const parts = fileName.split('.');
        return parts.length > 1 ? parts[parts.length - 1] : 'default';
      },
      calculateDiskUsagePercentage(bytes) {
        const limitInBytes = this.space * 1024 * 1024; // 5000MB in bytes
        const percentage = (bytes / limitInBytes) * 100;
        //  100%
        const limitedPercentage = Math.min(percentage, 100);
        return limitedPercentage.toFixed(2); // Display percentage with two decimal places
      },
      async handleDrop(targetTitle) {
        try {
          // Set the drop target
          this.dropTarget = targetTitle;
          debug.log("Dropped on:", this.dropTarget);

          // Assuming moveFile returns a promise
          await this.moveFile(this.draggedItem, this.dropTarget);

          // Perform any other necessary actions when an item is dropped
        } catch (error) {
          console.error("Error handling drop:", error);
          // Handle the error as needed
        }
      },
      getFileName(fileName) {
        const lastDotIndex = fileName.lastIndexOf('.');
        return lastDotIndex === -1 ? fileName : fileName.substring(0, lastDotIndex);
      },
      getFileExtension(fileName) {
        const lastDotIndex = fileName.lastIndexOf('.');
        return lastDotIndex === -1 ? '' : fileName.substring(lastDotIndex);
      },
      async resizeImageAndReplace(originalPath) {
        try {
          const imageUrl = this.schoolSession.s3Endpoint + originalPath;
          // Load the original image using image-js
          const originalImage = await Image.load(imageUrl);
          // Create a new key for the original image in the "folder_original" path
          const originalPathArray = originalPath.split("/");
          const originalFileName = originalPathArray.pop();
          const folderOriginalKey =
            originalPathArray.join("/") + "/folder_original/" + originalFileName;
          // Move the original image to "folder_original"
          const copyData = await this.S3.send(new CopyObjectCommand({
            Bucket: this.schoolSession.s3Bucket,
            CopySource: this.schoolSession.s3Bucket + "/" + originalPath,
            Key: folderOriginalKey,
            ACL: "public-read", // Set the appropriate ACL
          }));
          debug.log("Moved original image to folder_original:", copyData);
          // Resize the original image
          const resizedImage = originalImage.resize({
            width: 1980, // Set the desired width for the resized image
          });
          // Convert the resized image to Blob format with increased quality
          const resizedImageBlob = await resizedImage.toBlob("image/png", 1); // Use PNG format for lossless quality
          // Upload the resized image to replace the original image's place
          const resizedImageKey = originalPath;
          const resizeData = await this.S3.send(new PutObjectCommand({
            Bucket: this.schoolSession.s3Bucket,
            Key: resizedImageKey,
            Body: resizedImageBlob,
            ACL: "public-read", // Set the appropriate ACL
          }));
          debug.log("Resized and replaced image:", resizeData);
          // Refresh the file browser after updating the image
          this.listFile();
        } catch (error) {
          debug.log("Error resizing and replacing image:", error);
        }
      },
      async batchResizeImages() {
    try {
      // Filter only image files
      const imageFiles = this.fileListing.filter(file => {
        const extension = file.name.split('.').pop().toLowerCase();
        return ['jpg', 'jpeg', 'png', 'gif'].includes(extension);
      });

      if (imageFiles.length === 0) {
        toast({ type: 'info', message: '' });
        return;
      }

      this.isBatchResizing = true;
      this.totalBatchFiles = imageFiles.length;
      this.processedFiles = 0;
      this.batchProgress = 0;

      this.toast = toast({ 
        type: 'pending', 
        message: ` ${this.totalBatchFiles} ...` 
      });

      // Process images in sequence
      for (const file of imageFiles) {
        try {
          // Generate timestamp for file names
          const timestamp = new Date().getTime();

          // 1. Get image metadata
          const metadataResponse = await fetch('https://api.apyhub.com/processor/image/metadata/file-urls', {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json',
              'apy-token': 'APY02grbaFd83fKSDc8QtNTdld6dgFG4YDna2AIZYh4QGsE1jPsLQDBwuyM77R21Fq7BsSMHAH'
            },
            body: JSON.stringify({
              url: this.schoolSession.s3Endpoint + file.path
            })
          });

          const metadata = await metadataResponse.json();
          
          if (!metadata.data) {
            throw new Error('Failed to get image metadata');
          }

          const originalWidth = metadata.data.ImageWidth;
          const originalHeight = metadata.data.ImageHeight;

          // 2. Calculate new dimensions maintaining aspect ratio
          const maxWidth = 1920;
          let newWidth = originalWidth;
          let newHeight = originalHeight;

          if (originalWidth > maxWidth) {
            const ratio = maxWidth / originalWidth;
            newWidth = maxWidth;
            newHeight = Math.round(originalHeight * ratio);
          }

          let processedImageUrl = this.schoolSession.s3Endpoint + file.path;
          
          if (originalWidth > maxWidth) {
            const resizeResponse = await fetch(
              `https://api.apyhub.com/processor/image/resize/file-urls?output=resized-${timestamp}&width=${newWidth}&height=${newHeight}&auto_orientation=false&preserve_format=true`, 
              {
                method: 'POST',
                headers: {
                  'Content-Type': 'application/json',
                  'apy-token': 'APY02grbaFd83fKSDc8QtNTdld6dgFG4YDna2AIZYh4QGsE1jPsLQDBwuyM77R21Fq7BsSMHAH'
                },
                body: JSON.stringify({
                  url: processedImageUrl
                })
              }
            );

            const resizeResult = await resizeResponse.json();
            if (!resizeResult.data) {
              throw new Error('Resize API did not return expected data');
            }
            processedImageUrl = resizeResult.data;
          }

          // 4. Then compress
          const compressionPercent = Math.round(this.selectedImageQuality * 100);
          
          // Download the resized/original image
          const imageResponse = await fetch(processedImageUrl);
          const imageBlob = await imageResponse.blob();

          // Create FormData for compression
          const formData = new FormData();
          formData.append('image', imageBlob, 'image.jpg');

          // Compress the image
          const compressResponse = await fetch(
            `https://api.apyhub.com/processor/image/compress/file/url?output=final-${timestamp}&compression_percentage=${compressionPercent}&preserve_format=true`, 
            {
              method: 'POST',
              headers: {
                'apy-token': 'APY02grbaFd83fKSDc8QtNTdld6dgFG4YDna2AIZYh4QGsE1jPsLQDBwuyM77R21Fq7BsSMHAH'
              },
              body: formData
            }
          );

          const compressResult = await compressResponse.json();
          
          if (!compressResult.data) {
            throw new Error('Compress API did not return expected data');
          }

          // Download final image
          const finalImageResponse = await fetch(compressResult.data);
          const finalBlob = await finalImageResponse.blob();

          // Upload to S3
          const parallelUploads3 = new Upload({
            client: this.S3,
            params: {
              Bucket: this.schoolSession.s3Bucket,
              Key: file.path,
              Body: finalBlob,
              ACL: 'public-read',
              ContentType: finalBlob.type
            }
          });

          await parallelUploads3.done();

          // Update file size in database
          await this.resizeFile(file._id, finalBlob.size);

          // Update progress
          this.processedFiles++;
          this.batchProgress = (this.processedFiles / this.totalBatchFiles) * 100;

        } catch (error) {
          console.error(`Error processing image ${file.name}:`, error);
          toast({ 
            type: 'error', 
            message: ` ${file.name}` 
          });
        }
      }

      // Final update
      await this.listFile();
      this.toast.hide(` ${this.processedFiles}  ${this.totalBatchFiles} `, 'success');

    } catch (error) {
      console.error('Batch resize error:', error);
      this.toast.hide('', 'error');
    } finally {
      this.isBatchResizing = false;
    }
  },
      async handleResizeImage() {
        await this.resizeImage({
          resize: {
            enabled: true,
            maxWidth: 1920 //  UI
          },
          compress: {
            enabled: true,
            quality: this.selectedImageQuality || 0.9 // 
          },
          prompt: {
            enabled: false,
            title: '',
            message: ' ?'
          }
        });
      },
      async resizeOption(path, id) {
        this.fileToResize = {
          path: path,
          id: id
        };
        this.isResizeModalOpen = true;
      },
      async resizeImage(options = { 
        resize: {
          enabled: true,
          maxWidth: 1920
        }, 
        compress: {
          enabled: true,
          quality: 0.8
        },
        prompt: {
          enabled: true,
          title: '',
          message: '?'
        }
      }) {
        try {
          // Show confirmation dialog if prompt is enabled
          if (options.prompt?.enabled) {
            return new Promise((resolve) => {
              dialog.confirm({
                title: options.prompt.title,
                message: options.prompt.message,
                confirm: async () => {
                  await this.processImage(options);
                  resolve();
                },
                cancel: () => {
                  this.isResizeModalOpen = false;
                  resolve();
                }
              });
            });
          } else {
            // Process directly if no prompt needed
            await this.processImage(options);
          }
        } catch (error) {
          console.error('Error in resizeImage:', error);
          this.toast.hide('', 'error');
        }
      },

      // Move the main processing logic to a separate method
      async processImage(options) {
        try {
          this.toast = toast({ type: 'pending', message: '...' });
          const timestamp = new Date().getTime();
          let processedImageUrl = this.schoolSession.s3Endpoint + this.fileToResize.path;
          let finalBlob = null;

          // 1. Get image metadata first
          const metadataResponse = await fetch('https://api.apyhub.com/processor/image/metadata/file-urls', {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json',
              'apy-token': 'APY02grbaFd83fKSDc8QtNTdld6dgFG4YDna2AIZYh4QGsE1jPsLQDBwuyM77R21Fq7BsSMHAH'
            },
            body: JSON.stringify({
              url: processedImageUrl
            })
          });

          const metadata = await metadataResponse.json();
          if (!metadata.data) {
            throw new Error('Failed to get image metadata');
          }

          const originalWidth = metadata.data.ImageWidth;
          const originalHeight = metadata.data.ImageHeight;

          // 2. Calculate new dimensions maintaining aspect ratio
          const maxWidth = options.resize.maxWidth;
          let newWidth = originalWidth;
          let newHeight = originalHeight;

          if (originalWidth > maxWidth) {
            const ratio = maxWidth / originalWidth;
            newWidth = maxWidth;
            newHeight = Math.round(originalHeight * ratio);
          }

          // Skip resize if image is already smaller and resize option is enabled
          if (originalWidth <= maxWidth && options.resize.enabled) {
            this.toast.hide(' ' + maxWidth + 'px  ', 'info');
          }

          // 3. Resize if needed
          if (originalWidth > maxWidth && options.resize.enabled) {
            const resizeResponse = await fetch(
              `https://api.apyhub.com/processor/image/resize/file-urls?output=resized-${timestamp}&width=${newWidth}&height=${newHeight}&auto_orientation=false&preserve_format=true`, 
              {
                method: 'POST',
                headers: {
                  'Content-Type': 'application/json',
                  'apy-token': 'APY02grbaFd83fKSDc8QtNTdld6dgFG4YDna2AIZYh4QGsE1jPsLQDBwuyM77R21Fq7BsSMHAH'
                },
                body: JSON.stringify({
                  url: processedImageUrl
                })
              }
            );

            const resizeResult = await resizeResponse.json();
            if (!resizeResult.data) {
              throw new Error('Resize API did not return expected data');
            }
            processedImageUrl = resizeResult.data;
          }

          // 4. Compress if needed
          if (options.compress?.enabled) {
            const compressionPercent = Math.round(options.compress.quality * 100);
            
            // Download the current image (either resized or original)
            const imageResponse = await fetch(processedImageUrl);
            const imageBlob = await imageResponse.blob();

            // Create FormData for compression
            const formData = new FormData();
            formData.append('image', imageBlob, 'image.jpg');

            // Compress the image
            const compressResponse = await fetch(
              `https://api.apyhub.com/processor/image/compress/file/url?output=final-${timestamp}&compression_percentage=${compressionPercent}&preserve_format=true`, 
              {
                method: 'POST',
                headers: {
                  'apy-token': 'APY02grbaFd83fKSDc8QtNTdld6dgFG4YDna2AIZYh4QGsE1jPsLQDBwuyM77R21Fq7BsSMHAH'
                },
                body: formData
              }
            );

            const compressResult = await compressResponse.json();
            if (!compressResult.data) {
              throw new Error('Compress API did not return expected data');
            }
            processedImageUrl = compressResult.data;
          }

          // 5. Final processing and upload
          if (options.resize.enabled || options.compress?.enabled) {
            // Get the final processed image
            const finalImageResponse = await fetch(processedImageUrl);
            finalBlob = await finalImageResponse.blob();

            // Upload to S3
            const parallelUploads3 = new Upload({
              client: this.S3,
              params: {
                Bucket: this.schoolSession.s3Bucket,
                Key: this.fileToResize.path,
                Body: finalBlob,
                ACL: 'public-read',
                ContentType: finalBlob.type
              }
            });

            await parallelUploads3.done();

            // Update file size in database
            await this.resizeFile(this.fileToResize.id, finalBlob.size);
          }

          // 6. Success message
          const resizeInfo = originalWidth > maxWidth && options.resize.enabled ? 
            `(${originalWidth}x${originalHeight} -> ${newWidth}x${newHeight})` : 
            '()';

          const qualityInfo = options.compress?.enabled ? 
            `: ${Math.round(options.compress.quality * 100)}%` : '';
          
          const operationsInfo = [
            options.resize.enabled ? '' : '',
            options.compress?.enabled ? '' : ''
          ].filter(Boolean).join('');

          this.toast.hide(`${operationsInfo} ${resizeInfo} ${qualityInfo}`, 'success');
          this.isResizeModalOpen = false;
          await this.listFile();

        } catch (error) {
          console.error('Error processing image:', error);
          this.toast.hide('', 'error');
        }
      },
      async createThumbnail(originalPath, id) {
        try {
          const imageUrl      = originalPath;
          const originalImage = await Image.load(imageUrl);
          // Resize the original image to a width of 200 pixels
          const resizedImage  = originalImage.resize({
            width: 200,
          });
          // Convert the resized image to Blob with 20% quality (0.2)
          const resizedImageBlob = await resizedImage.toBlob('image/jpeg', 0.2);
          // Convert the Blob to Base64
          const resizedImageBase64 = await new Promise((resolve) => {
            const reader = new FileReader();
            reader.onloadend = () => {
              resolve(reader.result);
            };
            reader.readAsDataURL(resizedImageBlob);
          });
          // Assuming this.thumbnailFile is an asynchronous function, await it here
          await this.thumbnailFile(id, resizedImageBase64);
          return resizedImageBase64;
        } catch (error) {
          debug.log("Error resizing image:", error);
          return null; // Return null or handle the error as needed
        }
      },
      async captureThumbnail(videoUrl, id) {
        try {
          const video = document.createElement('video');
          video.src = videoUrl;
          video.crossOrigin = 'anonymous'; // Set crossorigin attribute
          await video.load();
          video.width = 640;
          video.height = 360;

          const canvas = document.createElement('canvas');
          canvas.width = video.width;
          canvas.height = video.height;
          const ctx = canvas.getContext('2d');
          await new Promise((resolve) => {
            video.addEventListener('canplay', resolve);
          });
          video.play();

          // Get video duration
          const videoDuration = video.duration;
          
          // Wait for the video to play for a certain duration (5 seconds in this case)
          await new Promise((resolve) => setTimeout(resolve, 5000));
          ctx.drawImage(video, 0, 0, canvas.width, canvas.height);
          video.pause();
          const thumbnailDataUrl = canvas.toDataURL('image/jpeg');
          this.capturedThumbnail = thumbnailDataUrl;

          // Now you have both the thumbnail and video duration
          debug.log('Thumbnail Data URL:', thumbnailDataUrl);
          debug.log('Video Duration:', videoDuration);

          // You can call your thumbnailFile function here with the video duration
          await this.thumbnailFile(id, thumbnailDataUrl, videoDuration);
        } catch (error) {
          debug.log('Error capturing thumbnail:', error);
        }
      },
      async restoreOriginalImage(resizedImageUrl) {
        try {
          // Extract the original image path from the resized image URL
          const originalPath = resizedImageUrl.replace(this.schoolSession.s3Endpoint, '');
          // Create the key for the original image in the "folder_original" path
          const originalPathArray = originalPath.split('/');
          const originalFileName = originalPathArray.pop();
          const originalKey = originalPathArray.join('/') + '/folder_original/' + originalFileName;
          // Copy the original image from "folder_original" to its original location
          const copyData = await this.S3.send(new CopyObjectCommand({
            Bucket: this.schoolSession.s3Bucket,
            CopySource: this.schoolSession.s3Bucket + '/' + originalKey,
            Key: originalPath,
            ACL: 'public-read', // Set the appropriate ACL
          }));
          debug.log('Restored original image:', copyData);
          // Delete the original image from "folder_original"
          const deleteData = await this.S3.send(new DeleteObjectCommand({
            Bucket: this.schoolSession.s3Bucket,
            Key: originalKey,
          }));
          debug.log('Deleted original image from folder_original:', deleteData);
          // Refresh the file browser after restoring the original image
          this.listFile();
        } catch (error) {
          debug.log('Error restoring original image:', error);
        }
      },
      isAllowedFile(filename) {
        const ext = filename.split('.').pop().toLowerCase();
        return this.AllowFile.includes(ext);
      },
      videoStreaming() {
        const _self     = this
        var url         = _self.videoContent;
        const video     = this.$refs.videoStreaming;

        let extension   = url.split(".").pop();
        debug.log("tage url", url);
        debug.log("tage video", video);
        debug.log("extension video", extension);
        const defaultOptions = {};

          const player = new Plyr(video, defaultOptions);
          player.on('timeupdate', (event) => {
            const instance = event.detail.plyr;
            debug.log(instance);
          });
      },
      formatDate(dateString) {
        //  dateString  null  undefined
        if (!dateString) {
          return '-';
        }
        
        try {
          const date = new Date(dateString);
          
          // 
          if (isNaN(date.getTime())) {
            return '-';
          }
          
          return new Intl.DateTimeFormat('th-TH', {dateStyle: 'long'}).format(date);
        } catch (error) {
          console.warn('Error formatting date:', dateString, error);
          return '-';
        }
      },
      onDoubleClick(filename) {
        // Find the item in this.fileListing with the matching name
        const selectedItem = this.fileListing.find(item => item.name === filename);

        if (!selectedItem) {
          //debug.log(`Item with name '${filename}' not found.`);
          return;
        }

        //debug.log("filename", filename + "/" + selectedItem);

        console.log("selectedItem", selectedItem);
        
        if (selectedItem.type === 'folder') {
          // Handle folder double-click
          // You can add your folder-specific logic here
          //debug.log("Folder double-clicked:", selectedItem);
        } else {
          // Handle file double-click
          const filetype = selectedItem.name.split('.').pop();
          this.viewFilesize = selectedItem.size;
          this.viewFilename = selectedItem.name;
          this.viewFilecover = selectedItem.cover;
          this.viewFilecreate = selectedItem.createdAt || selectedItem.create || null;
          this.viewFileurl = selectedItem.path;
          this.viewFileDuration = selectedItem.duration;
          this.viewFileThumbnail = selectedItem.thumbnail;
          this.viewFileextension = filetype;
          //debug.log("filetype", filetype);

          //debug.log("filename", this.viewFileurl);
          const fileExtension = this.viewFileurl.split('.').pop();
          //debug.log("fileExtension", fileExtension);
          if (['jpg', 'jpeg', 'png', 'gif'].includes(fileExtension)) {
            this.selectedFile = this.viewFileurl;
            this.modalContent = "image";
            this.showModal = true;
          } else if (['mp4', 'ogg', 'webm'].includes(fileExtension)) {
            console.log(selectedItem?.stream);

            if (selectedItem?.stream && selectedItem?.stream?.streamStatus!="ready") {
              // Start polling for stream status updates
              this.pollStreamStatus(this.selectItem, selectedItem?.stream?.streamId);
            }
            this.selectedFile = this.viewFileurl;
            this.modalContent = "video";
            this.showModal = true;
          } else if (['pdf'].includes(fileExtension)) {
            this.selectedFile = this.viewFileurl;
            this.modalContent = "pdf";
            this.showModal = true;
          }
          else
          {
            this.error = true;
            this.errorText = '';
          }
        }
      },
      isImage(src) {
        return src.match(/\.(jpeg|jpg|gif|png)$/) != null;
      },
      async listFile() {
        try {
          //this.toast = toast({ type: 'pending', message: '...' });
          const session     = storageManager.get('session');

          this.fileList     = [];
          this.prefix       = session.prefix;
          //this.activeBlock  = true;
          let andConditions = [
            this.session.current._id ? { owner: this.session.current._id, parent: this.prefix } : { owner: this.configs.siteID, parent: this.prefix }
          ];
          const pipeline = [
            {
              $match: {
                $and: andConditions,
              },
            },
            {
              $facet: {
                totalCount: [
                  { $count: "count" },
                ],
                folders: [
                  {
                    $match: {
                      mimetype: "folder",
                    },
                  },
                  {
                    $lookup: {
                      from: "storage", // Target collection
                      let: { folder_path: "$path", owner_id: "$owner" }, // Variables from the current document (folder)
                      pipeline: [
                        {
                          $match: {
                            $expr: {
                              $and: [
                                { $eq: ["$parent", "$$folder_path"] }, // Match files directly in this folder
                                { $eq: ["$owner", "$$owner_id"] },   // Match owner
                                { $ne: ["$mimetype", "folder"] }   // Exclude sub-folders from size calculation
                              ]
                            }
                          }
                        },
                        {
                          $group: { // Group files within the folder
                            _id: "$parent", // Group by the parent path (which is $$folder_path)
                            totalSize: { $sum: { $ifNull: ["$size", 0] } }, // Sum sizes
                            fileCount: { $sum: 1 } // Count the files
                          }
                        },
                        {
                            $project: { // Reshape for lookup result
                                _id: 0,
                                size: "$totalSize",
                                count: "$fileCount"
                            }
                        }
                      ],
                      as: "folderStats" // Name the output array
                    }
                  },
                  {
                    $project: {
                      _id: 1,
                      original: 1,
                      path: 1,
                      name: 1,
                      type: 1,
                      duration: 1,
                      mimetype: 1,
                      spaceId: 1,
                      createdAt: 1,
                      stream: 1,
                      transcode: 1,
                      // Extract size and count from the lookup result array (it should have 0 or 1 element)
                      size: { $ifNull: [ { $arrayElemAt: ["$folderStats.size", 0] }, 0 ] },
                      count: { $ifNull: [ { $arrayElemAt: ["$folderStats.count", 0] }, 0 ] }
                    },
                  },
                ],
                files: [
                  {
                    $match: {
                      mimetype: { $ne: "folder" }, // Exclude folders
                    },
                  },
                  {
                    $project: {
                      _id: 1,
                      original: 1,
                      path: 1,
                      name: 1,
                      type: 1,
                      duration: 1,
                      mimetype: 1,
                      spaceId: 1,
                      createdAt: 1,
                      stream: 1,
                      transcode: 1,
                      size: { $ifNull: ["$size", 0] },
                    },
                  },
                ],
              },
            },
          ];

          const payload = {
            pipeline: pipeline,
          };

          const { data } = await this.$Request.POST('storage/aggregate', payload, this.hostkey);
          // Extract folders and files from the pipeline result
          const folders = data[0].folders;
            const files   = data[0].files;

            // Sort folders and files naturally
            folders.sort((a, b) => a.name.localeCompare(b.name, undefined, { numeric: true, sensitivity: 'base' }));
            files.sort((a, b)   => a.name.localeCompare(b.name, undefined, { numeric: true, sensitivity: 'base' }));

            // Concatenate folders and files while keeping the order (folders first)
            this.fileListing = folders.concat(files);

            this.folderPath = this.prefix;

            if(this.folderPath!="" && this.folderPath!=undefined) {
              this.folderPath = this.folderPath.split("/");
            } else {
              this.folderPath = "";
            }

            const size = await this.usageSpace();
            this.summaryFileSize = size
            this.unselectItem()
        } catch (error) {
          debug.log(error);
        }
      },
      async usageSpace() {
        try {
          const pipeline = [
            {
              $match: {
                owner: this.session.current._id || this.configs.siteID,
              },
            },
            {
              $group: {
                _id: null,
                totalSize: { $sum: "$size" }, // Replace "size" with the actual field name containing the size
              },
            },
          ];

          const payload = {
            pipeline: pipeline,
          };

          const { data } = await this.$Request.POST('storage/aggregate', payload, this.hostkey);

          return data[0].totalSize;

        } catch (error) {
          debug.log(error);
        }
      },
