        console.log(fileUrl,fileName,this.selectItem);
        try {
          // Request payload
          const payload = {
            url: fileUrl,
            meta: { name: fileName }
          };

          // Send request
          const response = await fetch("https://gateway.cloudrestfulapi.com/vdo/copy", {
            method: "POST",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify(payload),
          });

          const result = await response.json();
          if (!result.success) {
            throw new Error("Streaming request failed.");
          }

          debug.log("Stream Response:", result);

          // Extract required data
          const { uid, thumbnail, playback, status } = result;

          // Update file database entry
          await this.updateFileStreamingData(this.selectItem, {
            streamId: uid,
            streamThumbnail: thumbnail,
            streamPlayback: playback,
            streamStatus: status,
          });

          debug.log("File updated with streaming data.");
          this.toast = toast({ type: "success", message: "Streaming request successful!" });

          // Start polling for stream status updates
          this.pollStreamStatus(this.selectItem, uid);

          this.toast.hide();

        } catch (error) {
          console.error("Error in streaming request:", error);
          toast({ type: "error", message: "Failed to start streaming." });
        }
      },

      async updateFileStreamingData(fileId, streamData) {
        try {
          const payload = {
            data: {
              stream: {  // Fixed typo from "srteam" to "stream"
                streamId: streamData.streamId,
                streamThumbnail: streamData.streamThumbnail,
                streamPlayback: streamData.streamPlayback,
                streamStatus: streamData.streamStatus,
              }
            },
            options: {}
          };

          const { status } = await this.$Request.PUT(`storage/${fileId}`, payload, this.hostkey);

          if (status === 200) {
            await this.listFile();
          }
        } catch (error) {
          debug.log(error);
        }
      },
      async pollStreamStatus(fileId, streamId, retries = 10, interval = 30000) {
        let attempts = 0;
        
        const checkStatus = async () => {
          console.log("checkStatus");
          if (attempts >= retries) {
            debug.log("Max retries reached for stream status check.");
            return;
          }
          attempts++;

          try {
            const response = await fetch(`https://gateway.cloudrestfulapi.com/vdo/stream/${streamId}`, {
              method: "GET",
              headers: { "Content-Type": "application/json" },
            });

            if (!response.ok) {
              throw new Error("Failed to fetch stream status.");
            }

            const streamData = await response.json();
            console.log("checkStatus",streamData.status);
            // If status is NOT 'downloading', update file data
            if (this.selectItemData.stream.streamStatus != 'ready') {
              console.log("Update");
              const updatedStreamData = {
                streamId: streamData.uid,
                streamThumbnail: streamData.thumbnail,
                streamPlayback: streamData.playback,
                streamStatus: streamData.status,
              };

              await this.updateFileStreamingData(fileId, updatedStreamData);
              await this.listFile();
              debug.log("Stream status updated successfully.");
              return;
            }
          } catch (error) {
            debug.log("Error fetching stream status:", error);
          }

          // Retry after interval if still downloading
          setTimeout(checkStatus, interval);
        };

        checkStatus();
      },
      async deleteStream(streamId, fileId) {
        try {
          // Confirm deletion with user
          const confirmDelete = confirm("Are you sure you want to delete this stream?");
          if (!confirmDelete) return;

          // Send DELETE request to API
          const response = await fetch(`https://gateway.cloudrestfulapi.com/vdo/stream/${streamId}`, {
            method: "DELETE",
            headers: { "Content-Type": "application/json" },
          });

          if (!response.ok) {
            throw new Error("Failed to delete stream.");
          }

          // Remove stream data from the database
          await this.removeStreamData(fileId);
          this.showStreamMenu = false
          // Show success message
          alert("Stream deleted successfully.");

          // Refresh the file list
          
        } catch (error) {
          console.error("Error deleting stream:", error);
          alert("Failed to delete stream. Please try again.");
        }
      },
      async removeStreamData(fileId) {
        try {
          const payload = {
            data: {
              stream: null, // Removing the stream data
            },
            options: {},
          };

          const { status } = await this.$Request.PUT(`storage/${fileId}`, payload, this.hostkey);

          if (status === 200) {
            await this.listFile();
            console.log("Stream data removed successfully.");
          } else {
            console.error("Failed to remove stream data.");
          }
        } catch (error) {
          console.error("Error removing stream data:", error);
        }
      },
      async submitFiles() {
  try {
    //this.toast = toast({ type: 'pending', message: '...' });

    if (!this.files.length) {
      throw new Error('');
    }

    const currentDate = new Date();
    const year = currentDate.getFullYear().toString();
    const month = (currentDate.getMonth() + 1).toString().padStart(2, '0');

    for (let i = 0; i < this.files.length; i++) {
      const file = this.files[i];
      
      if (!file || file.size === 0) {
        console.error(`Error: File is unreadable or empty - ${file?.name || "Unknown file"}`);
        file.status = 'error';
        continue;
      }

      try {
        const file_name_array = file.name.split('.');
        const file_extension = file_name_array.pop();
        const timestamp = new Date().getTime();
        const new_file_name = String(timestamp);
        const path = `${year}/${month}/`;
        
        // 
        file.status = 'uploading';
        file.progress = 0;
        this.$forceUpdate();

        //  multipart upload
        const parallelUploads3 = new Upload({
          client: this.S3,
          params: {
            Bucket: this.schoolSession.s3Bucket,
            Key: `${path}${new_file_name}.${file_extension}`,
            Body: file,
            ACL: 'public-read',
            ContentType: file.type
          },
          queueSize: 4, // optional concurrency configuration
          partSize: 1024 * 1024 * 5, // optional size of each part, in bytes, at least 5MB
          leavePartsOnError: false, // optional manually handle dropped parts
        });

        //  reference  upload controller
        this.uploadControllers.set(file.name, parallelUploads3);

        // 
        parallelUploads3.on("httpUploadProgress", (progress) => {
          // 
          if (file.status === 'cancelled') {
            return;
          }

          const loaded = progress.loaded || 0;
          const total = progress.total || file.size;
          const percentLoaded = Math.round((loaded * 100) / total);
          
          file.progress = percentLoaded;
          this.$forceUpdate();
          
          debug.log(`Upload Progress for ${file.name}: ${percentLoaded}%`);
        });

        // 
        await parallelUploads3.done();

        //  controller 
        this.uploadControllers.delete(file.name);

        // 
        if (file.status === 'cancelled') {
          continue;
        }

        // 
        file.status = 'success';
        file.progress = 100;
        file.original = `${path}${new_file_name}.${file_extension}`;

        // 
        const payload = {
          data: {
            owner: this.session.current._id || this.configs.siteID,
            original: file.original,
            path: this.schoolSession.s3Endpoint + file.original,
            parent: this.prefix,
            name: file.name,
            size: file.size,
            type: this.getFileType(file.type),
            mimetype: file.type,
            spaceId: this.session.current.spaceId || this.configs.spaceId,
          },
          options: {}
        };

        const response = await this.$Request.POST('storage/', payload, this.hostkey);

        //  thumbnail 
        if (file.type.startsWith('image/')) {
          await this.createThumbnail(this.schoolSession.s3Endpoint + file.original, response.data._id);
            // First set the fileToResize object
            this.fileToResize = {
              path: file.original,
              id: response.data._id
            };
            /*
            // For both resize and compress with custom settings
            await this.resizeImage({
              resize: {
                enabled: false,
                maxWidth: 1920 // or any other width
              },
              compress: {
                enabled: true,
                quality: 0.9
              },prompt: {
                enabled: true,
                title: '',
                message: ' ?'
              }
            });
            */
        } else if (file.type.startsWith('video/')) {
          await this.captureThumbnail(this.schoolSession.s3Endpoint + file.original, response.data._id);
        }

      } catch (error) {
        //  error 
        if (error.name === 'AbortError') {
          file.status = 'cancelled';
          debug.log(`Upload cancelled for file: ${file.name}`);
        } else {
          console.error(`Upload Failed for file: ${file.name}`, error);
          file.status = 'error';
          file.progress = 0;
          this.error = true;
          this.errorText = ` ${file.name} : ${error.message}`;
        }
        
        //  controller  error 
        this.uploadControllers.delete(file.name);
      }

      this.$forceUpdate();
    }

    // 
    const successFiles = this.files.filter(f => f.status === 'success');
    if (successFiles.length > 0) {
      await this.listFile();
    }

    //  (success, error, cancelled)
    this.files = this.files.filter(f => f.status === 'pending');
    
    if (this.files.length === 0) {
      this.uploadPanel = false;
    }

    // 
    if (successFiles.length > 0) {
      //this.toast.hide(` ${successFiles.length} `, 'success');
    }

  } catch (error) {
    console.error('Submit Files Error:', error);
    this.toast.hide(error.message, 'error');
    this.error = true;
    this.errorText = error.message;
  }
},

// 
async cancelUpload(fileName) {
  const upload = this.uploadControllers.get(fileName);
  if (upload) {
    try {
      await upload.abort();
      this.uploadControllers.delete(fileName);
      
      const file = this.files.find(f => f.name === fileName);
      if (file) {
        file.status = 'cancelled';
        file.progress = 0;
      }
      
      this.$forceUpdate();
    } catch (error) {
      debug.log(`Error cancelling upload for ${fileName}:`, error);
    }
  }
},
      handleFilesUpload() {
        let uploadedFiles = this.$refs.files.files;
        for (var i = 0; i < uploadedFiles.length; i++) {
          uploadedFiles[i].status = 'pending';
          this.files.push(uploadedFiles[i]);
          debug.log('Uploaded file name:', uploadedFiles[i].name);
