      async convertVideo(fileData) {
        const payload = new FormData();
        payload.append('url', fileData.path); //  path 
        payload.append('quality', this.selectedQuality); //  select
        payload.append('site', 'fti.academy'); // 
        payload.append('storage', fileData._id); //  ID 

        try {
          const response = await fetch('https://media.cloudrestfulapi.com/convert', {
            method: 'POST',
            body: payload,
          });

          if (!response.ok) {
            throw new Error('Failed to convert video');
          }

          const result = await response.json();
          console.log('Conversion result:', result);
          // Handle success (e.g., show a success message or update the UI)
        } catch (error) {
          console.error('Error converting video:', error);
          // Handle error (e.g., show an error message)
        }
      },
      getDocumentIcon(extension) {
        const iconMap = {
          'pdf': 'file-pdf',
          'doc': 'file-word',
          'docx': 'file-word',
          'xls': 'file-excel',
          'xlsx': 'file-excel',
          'ppt': 'file-powerpoint',
          'pptx': 'file-powerpoint',
          'zip': 'file-archive',
          'rar': 'file-archive',
          '7z': 'file-archive',
          'txt': 'file-alt',
          'csv': 'file-csv',
          'js': 'file-code',
          'html': 'file-code',
          'htm': 'file-code',
          'css': 'file-code',
          'xml': 'file-code',
          'json': 'file-code'
        };
        return iconMap[extension.toLowerCase()] || 'file';
      },
      getDocumentIconColor(extension) {
        const colorMap = {
          'pdf': 'text-red-500',
          'doc': 'text-blue-500',
          'docx': 'text-blue-500',
          'xls': 'text-green-500',
          'xlsx': 'text-green-500',
          'ppt': 'text-purple-500',
          'pptx': 'text-purple-500',
          'zip': 'text-yellow-500',
          'rar': 'text-yellow-500',
          '7z': 'text-yellow-500',
          'txt': 'text-gray-500',
          'csv': 'text-gray-700',
          'js': 'text-orange-500',
          'html': 'text-orange-500',
          'htm': 'text-orange-500',
          'css': 'text-blue-400',
          'xml': 'text-cyan-500',
          'json': 'text-green-400'
        };
        return colorMap[extension.toLowerCase()] || 'text-gray-400';
