      filterByType(type) {
        this.fileTypeFilter = type;
      },
      getFilterLabel(type) {
        const labels = {
          'all': '',
          'folder': '',
          'image': '',
          'media': '',
          'document': ''
        };
        return labels[type] || '';
      },
      getFilterCount(type) {
        if (type === 'all') {
          return this.fileListing.length;
        } else if (type === 'folder') {
          return this.fileListing.filter(f => f.mimetype === 'folder').length;
        } else {
          return this.fileListing.filter(f => f.type === type).length;
        }
      },
      async copyToClipboard(text) {
        try {
          await navigator.clipboard.writeText(text);
          toast({ type: 'success', message: '' });
        } catch (error) {
          // Fallback for older browsers
          const textArea = document.createElement('textarea');
          textArea.value = text;
          document.body.appendChild(textArea);
          textArea.select();
          document.execCommand('copy');
          document.body.removeChild(textArea);
          toast({ type: 'success', message: '' });
        }
      },
	},
  computed: {
    imageSource() {
      let filePath = this.selectFile.thumbnail || this.selectFile.path;
      return filePath;
    },
    sectionHeight() {
      const windowHeight = window.innerHeight;
      const sectionRatio = 0.6;
      const calculatedHeight = `${windowHeight * sectionRatio}px`;
      return calculatedHeight;
    },
    filteredFileList() {
      const query = this.searchQuery.trim().toLowerCase();
      if (!query) {
        return this.fileListing;
      }
      return this.fileListing.filter((file) => file.name.toLowerCase().includes(query));
    },
    computedDimensions() {
      return this.viewFileDimensions;
    },
    dragItemClass() {
      return this.dragging ? 'dragging' : '';
    },
    filteredQualities() {
        return ['420p', '720p', '1080p', '1920p']; // 
    },
    displayedFiles() {
      let files = this.filteredFileList;
      
      if (this.fileTypeFilter !== 'all') {
        if (this.fileTypeFilter === 'folder') {
          files = files.filter(f => f.mimetype === 'folder');
        } else {
          files = files.filter(f => f.type === this.fileTypeFilter);
        }
