      <div class="px-4 py-4 border-b border-gray-200">
        <h3 class="text-sm font-medium text-gray-700 mb-3"></h3>
        <div class="space-y-2">
          <button 
            @click="openUploadBox"
            class="w-full flex items-center gap-3 px-3 py-2 text-sm text-gray-600 hover:bg-gray-100 hover:text-gray-900 rounded-lg transition-colors duration-200"
          >
            <i class="fas fa-upload text-sm w-4"></i>
            <span></span>
          </button>
          <button 
            @click="openCreateFolder"
            class="w-full flex items-center gap-3 px-3 py-2 text-sm text-gray-600 hover:bg-gray-100 hover:text-gray-900 rounded-lg transition-colors duration-200"
          >
            <i class="fas fa-folder-plus text-sm w-4"></i>
            <span></span>
          </button>
          <button 
            @click="openModal()"
            class="w-full flex items-center gap-3 px-3 py-2 text-sm text-gray-600 hover:bg-gray-100 hover:text-gray-900 rounded-lg transition-colors duration-200"
          >
            <i class="fas fa-stream text-sm w-4"></i>
            <span> Streaming</span>
          </button>
          <button 
            @click="listFile"
            class="w-full flex items-center gap-3 px-3 py-2 text-sm text-gray-600 hover:bg-gray-100 hover:text-gray-900 rounded-lg transition-colors duration-200"
          >
            <i class="fas fa-sync-alt text-sm w-4"></i>
            <span></span>
          </button>
          <button 
            v-if="fileListing.filter(f => f.type === 'image').length > 0"
            @click="batchResizeImages"
            class="w-full flex items-center gap-3 px-3 py-2 text-sm text-gray-600 hover:bg-gray-100 hover:text-gray-900 rounded-lg transition-colors duration-200"
          >
            <i class="fas fa-magic text-sm w-4"></i>
            <span></span>
          </button>
        </div>
      </div>

      <!-- Storage Usage -->
      <div class="px-4 py-4 border-b border-gray-200">
        <h3 class="text-sm font-medium text-gray-700 mb-3"></h3>
        <div class="space-y-3">
          <div class="flex items-center justify-between text-sm">
            <span class="text-gray-600"></span>
            <span class="font-medium text-gray-900">{{ formatBytes(summaryFileSize) }}</span>
          </div>
          <div class="w-full bg-gray-200 rounded-full h-2">
            <div 
              class="h-2 rounded-full transition-all duration-300"
              :class="{
                'bg-green-500': calculateDiskUsagePercentage(summaryFileSize) < 70,
                'bg-yellow-500': calculateDiskUsagePercentage(summaryFileSize) >= 70 && calculateDiskUsagePercentage(summaryFileSize) < 90,
                'bg-red-500': calculateDiskUsagePercentage(summaryFileSize) >= 90
              }"
              :style="{ width: calculateDiskUsagePercentage(summaryFileSize) + '%' }">
            </div>
          </div>
          <div class="flex items-center justify-between text-xs text-gray-500">
            <span>{{ calculateDiskUsagePercentage(summaryFileSize) }}% </span>
            <span>{{ space }} MB</span>
          </div>
        </div>
      </div>

      <!-- User Info -->
      <div class="mt-auto px-4 py-4 flex-shrink-0">
        <div class="flex items-center gap-3 p-3 bg-gray-50 rounded-lg">
          <div class="w-8 h-8 bg-blue-500 rounded-full flex items-center justify-center">
            <i class="fas fa-user text-white text-sm"></i>
          </div>
          <div class="flex-1 min-w-0">
            <p class="text-sm font-medium text-gray-900 truncate">{{ session?.current?.name || '' }}</p>
            <p class="text-xs text-gray-500"></p>
          </div>
        </div>
      </div>
    </div>
  </div>

  <!-- Main Content -->
  <div class="flex-1 flex flex-col min-w-0 h-full">
    <!-- Mobile Header -->
    <div class="lg:hidden bg-white border-b border-gray-200 px-4 py-3">
      <div class="flex items-center justify-between">
        <h1 class="text-lg font-semibold text-gray-900">{{ PageName }}</h1>
        <button 
          @click="toggleMobileSidebar" 
          class="p-2 text-gray-600 hover:text-gray-900 hover:bg-gray-100 rounded-lg transition-colors"
        >
          <i class="fas fa-bars"></i>
        </button>
      </div>
    </div>

    <!-- Header Section -->
    <div class="bg-white border-b border-gray-200 px-4 py-3">
      <div class="flex flex-col md:flex-row md:items-center md:justify-between gap-3">
        <!-- Search Input -->
        <div class="flex-1 max-w-md">
          <div class="relative">
            <i class="fas fa-search absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 text-sm"></i>
            <input 
              type="text" 
              v-model="searchQuery" 
              placeholder="..." 
              class="w-full pl-10 pr-4 py-2 border border-gray-200 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent text-sm"
            />
          </div>
        </div>

        <!-- Action Buttons -->
        <div class="flex items-center space-x-2">
          <!-- Quick Action Buttons -->
          <div class="flex items-center space-x-2">
            <button 
              @click="openCreateFolder"
              class="inline-flex items-center px-3 py-2 bg-green-600 hover:bg-green-700 text-white rounded-lg text-sm font-medium transition-colors duration-200 shadow-sm"
            >
              <i class="fas fa-folder-plus mr-2 text-xs"></i>
              <span class="hidden sm:inline"></span>
            </button>
            <button 
              @click="openUploadBox"
              class="inline-flex items-center px-3 py-2 bg-blue-600 hover:bg-blue-700 text-white rounded-lg text-sm font-medium transition-colors duration-200 shadow-sm"
            >
              <i class="fas fa-upload mr-2 text-xs"></i>
              <span class="hidden sm:inline"></span>
            </button>
          </div>

          <!-- Layout Toggle -->
          <div class="flex items-center space-x-2 bg-gray-50 rounded-lg p-1">
            <button 
              @click="toggleLayout" 
              :class="layout === 'grid' ? 'bg-white shadow-sm' : 'hover:bg-gray-100'"
              class="p-2 rounded-md transition-all duration-200">
              <font-awesome-icon :icon="['fas', 'th-large']" class="w-4 h-4 text-gray-600" />
            </button>
            <button 
              @click="toggleLayout" 
              :class="layout === 'list' ? 'bg-white shadow-sm' : 'hover:bg-gray-100'"
              class="p-2 rounded-md transition-all duration-200">
              <font-awesome-icon :icon="['fas', 'list']" class="w-4 h-4 text-gray-600" />
            </button>
          </div>
          
          <button 
            v-if="Mode !== 'popup'"
            @click="openUploadBox" 
            class="lg:hidden bg-blue-600 hover:bg-blue-700 text-white font-medium px-4 py-2 rounded-lg transition-colors duration-200 text-sm flex items-center gap-2"
          >
            <i class="fas fa-upload text-xs"></i>
            
          </button>
        </div>
      </div>
    </div>

    <!-- Breadcrumb Navigation -->
    <div class="bg-white border-b border-gray-200 px-4 py-3">
      <nav class="flex items-center space-x-2" aria-label="Breadcrumb">
        <button 
          @click="openFolder()" 
          @dragover.prevent
          @drop="handleDrop('')"
          class="flex items-center text-sm text-gray-500 hover:text-blue-600 transition-colors duration-200 p-2 rounded-md hover:bg-blue-50">
          <font-awesome-icon :icon="['fas','home']" class="text-gray-400 mr-2 w-4 h-4"/>
          <span class="font-medium"></span>
        </button>
    
        <span v-for="(Path,index) in folderPath" :key="index" class="flex items-center">
          <font-awesome-icon :icon="['fas','chevron-right']" class="text-gray-300 w-3 h-3 mx-2"/>
          <button 
            v-if="(index+1)==folderPath.length" 
            class="flex items-center text-sm font-medium text-blue-600 p-2 rounded-md bg-blue-50">
            <font-awesome-icon :icon="['fas','folder-open']" class="text-blue-500 mr-2 w-4 h-4"/> 
            {{Path}}
          </button>
          <button 
            v-else
            @click="openFolder(getFullPath(Path))"
            @dragover.prevent
            @drop="handleDrop(getFullPath(Path))"
            class="flex items-center text-sm font-medium text-gray-600 hover:text-blue-600 transition-colors duration-200 p-2 rounded-md hover:bg-blue-50">
            <font-awesome-icon :icon="['fas','folder']" class="text-yellow-500 mr-2 w-4 h-4"/> 
            {{Path}}
          </button>
        </span>
      </nav>
    </div>

    <!-- File Type Filter (Mobile) -->
    <div class="lg:hidden bg-white border-b border-gray-200">
      <div class="px-4 py-2">
        <select 
          v-model="fileTypeFilter" 
          @change="filterByType(fileTypeFilter)"
          class="w-full border border-gray-200 rounded-lg px-3 py-2 text-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
        >
          <option value="all"> ({{ fileListing.length }})</option>
          <option value="folder"> ({{ fileListing.filter(f => f.mimetype === 'folder').length }})</option>
          <option value="image"> ({{ fileListing.filter(f => f.type === 'image').length }})</option>
          <option value="media"> ({{ fileListing.filter(f => f.type === 'media').length }})</option>
          <option value="document"> ({{ fileListing.filter(f => f.type === 'document').length }})</option>
        </select>
      </div>
    </div>

    <!-- Main Content Area -->
    <div class="flex-1 p-4" :class="Mode === 'popup' ? 'overflow-y-auto' : 'overflow-hidden'"
         :style="Mode === 'popup' ? { maxHeight: 'calc(70vh - 140px)' } : {}">
      <!-- Current Filter Info -->
      <div class="mb-4 flex items-center justify-between">
        <div class="flex items-center gap-2">
          <h2 class="text-lg font-semibold text-gray-900">
            {{ getFilterLabel(fileTypeFilter) }}
          </h2>
          <span class="px-2 py-1 bg-gray-100 text-gray-600 text-sm rounded-full">
            {{ displayedFiles.length }} 
          </span>
        </div>
        
        <!-- Error Display -->
        <div v-if="error" class="flex items-center space-x-2 bg-red-50 px-3 py-2 rounded-lg border border-red-200">
          <font-awesome-icon :icon="['fas', 'exclamation-triangle']" class="text-red-600 text-sm"/>
          <span class="text-sm font-medium text-red-800">{{ errorText }}</span>
          <button @click="error = false" class="text-red-600 hover:text-red-800 ml-2">
            <font-awesome-icon :icon="['fas', 'times']" class="text-xs"/>
          </button>
        </div>
      </div>

      <!-- File Content -->
      <div :class="Mode === 'popup' ? 'h-auto' : 'h-full overflow-y-auto'">
        <!-- Grid Layout -->
        <div v-if="layout === 'grid'" class="grid grid-cols-2 gap-4 sm:grid-cols-3 sm:gap-6 lg:grid-cols-4 xl:grid-cols-5 2xl:grid-cols-6">
          
          <div v-for="(file) in displayedFiles" :key="file._id" class="relative group" :class="{ 'ring-2 ring-blue-500 ring-offset-2': file._id === selectItem }">

            <div v-if="file.mimetype === 'folder'"
            :class="'folder-' + file._id"
            @dragover.prevent
            @drop.stop="handleDrop(file.path)"
            @click.stop="openViewFile(file.name)"
            :data-item="file.path"
            :ref="`dropTarget-${file.path}`"
            class="bg-white rounded-xl transition-all duration-200 overflow-hidden cursor-pointer border border-gray-200 hover:border-blue-300 group" 
            >
                <div class="folder-content"
                @contextmenu.stop="onContextMenu($event, file.name, file.name, file._id, file.count)"
                @dblclick.stop="openFolder(file.path)"
                >
                  <!-- Folder thumbnail area with same aspect ratio as files -->
                  <div class="aspect-square bg-gradient-to-br from-yellow-100 to-yellow-200 relative overflow-hidden flex items-center justify-center">
                    <font-awesome-icon :icon="['fas', 'folder']" class="text-yellow-500 text-6xl drop-shadow-sm group-hover:scale-105 transition-transform duration-200" />
                    <div class="absolute top-3 right-3 bg-blue-500 text-white text-xs px-2 py-1 rounded-full font-medium shadow-sm">
                      {{file.count}}
                    </div>
                    <!-- Hover overlay -->
                    <div class="absolute inset-0 bg-black bg-opacity-0 group-hover:bg-opacity-10 transition-all duration-200 flex items-center justify-center">
                      <font-awesome-icon :icon="['fas', 'folder-open']" class="text-yellow-600 text-2xl opacity-0 group-hover:opacity-100 transition-opacity duration-200" />
                    </div>
                  </div>

                  <!-- File info section (same as files) -->
                  <div class="p-3">
                    <p class="text-sm font-semibold text-gray-900 truncate mb-1" :title="file.name">{{file.name}}</p>
                    <div class="flex justify-between items-center text-xs text-gray-500">
                      <span>{{ this.formatBytes(file.size) }}</span>
                      <span>{{ file.count }} </span>
                    </div>
                  </div>
                </div>
            </div>

            <div v-if="file.mimetype!='folder'"
            draggable="true"
            @dragstart.stop="handleDragStart(file._id)"
            @dragend.stop="handleDragEnd"
            :data-item="file._id"
            :ref="`draggableItem-${file._id}`"
            class="bg-white rounded-xl transition-all duration-200 overflow-hidden cursor-pointer border border-gray-200 hover:border-blue-300 group"
            >
              <div v-if="file.type=='image'" @contextmenu.stop="onFileContextMenu($event, file.name, file.name, file._id, file.original)" @dblclick.stop="onDoubleClick(file.name)" @click.stop="openViewFile(file.name)" class="relative">
                
                <div class="aspect-square bg-gray-200 relative overflow-hidden">
                  <template v-if="file.thumbnail || file.path">
                    <img :src="file.thumbnail ? file.thumbnail : file.path" alt="" class="w-full h-full object-cover group-hover:scale-105 transition-transform duration-200">
                    <div class="absolute inset-0 bg-black bg-opacity-0 group-hover:bg-opacity-10 transition-all duration-200 flex items-center justify-center">
                      <font-awesome-icon :icon="['fas', 'image']" class="text-white text-2xl opacity-0 group-hover:opacity-100 transition-opacity duration-200" />
                    </div>
                  </template>
                  <template v-else>
                    <div class="w-full h-full flex items-center justify-center bg-gray-200">
                      <font-awesome-icon :icon="['fas', 'image']" class="text-gray-400 text-4xl"/>
                    </div>
                  </template>
                </div>

                <button type="button" class="absolute inset-0 focus:outline-none">
                  <span class="sr-only">View details for {{file.name}}</span>
                </button>
              </div>

              <div v-if="file.type=='document'" @contextmenu.stop="onFileContextMenu($event, file.name, file.name, file._id, file.original)" @dblclick.stop="onDoubleClick(file.name)"  @click.stop="openViewFile(file.name)" class="relative">
                
                <div class="aspect-square bg-gray-200 flex items-center justify-center relative">
                  <font-awesome-icon 
                    :icon="['fas', getDocumentIcon(getNewFileExtension(file.name))]" 
                    :class="getDocumentIconColor(getNewFileExtension(file.name))"
                    class="text-5xl"/>
                  <div
                    :class="{
                      'bg-gray-500': ['default', 'txt'].includes(getNewFileExtension(file.name)),
                      'bg-red-500': getNewFileExtension(file.name) === 'pdf',
                      'bg-green-500': ['xlsx', 'xls'].includes(getNewFileExtension(file.name)),
                      'bg-blue-500': ['docx', 'doc'].includes(getNewFileExtension(file.name)),
                      'bg-purple-500': ['pptx', 'ppt'].includes(getNewFileExtension(file.name)),
                      'bg-black': getNewFileExtension(file.name) === 'csv',
                      'bg-orange-500': getNewFileExtension(file.name) === 'js',
                      'bg-yellow-500': getNewFileExtension(file.name) === 'zip',
                      'bg-pink-500': ['jpg', 'jpeg', 'png', 'gif'].includes(getNewFileExtension(file.name)),
                      'bg-brown-500': ['html', 'htm'].includes(getNewFileExtension(file.name)),
                      'bg-cyan-500': ['xml'].includes(getNewFileExtension(file.name)),
                    }"
                    class="absolute top-3 right-3 text-white text-xs px-2 py-1 rounded-md font-medium shadow-sm"
                  >
                    {{ getNewFileExtension(file.name).toUpperCase() }}
                  </div>
                </div>
                <button type="button" class="absolute inset-0 focus:outline-none">
                  <span class="sr-only">View details for {{file.name}}</span>
                </button>
              </div>

              <div v-if="file.type=='media'" @contextmenu.stop="onFileContextMenu($event, file.name, file.name, file._id, file.original, file.duration)" @dblclick.stop="onDoubleClick(file.name)"  @click.stop="openViewFile(file.name)" class="relative">
                <div class="aspect-square bg-gray-200 relative overflow-hidden">
                  <template v-if="file.thumbnail">
                    <img :src="file.thumbnail" alt="" class="w-full h-full object-cover group-hover:scale-105 transition-transform duration-200" />
                    
                    <!-- Status badges -->
                    <div class="absolute top-2 left-2 flex flex-col space-y-1">
                      <div v-if="file?.stream" class="bg-green-500 text-white text-xs px-2 py-1 rounded-md font-medium shadow-sm flex items-center">
                        <font-awesome-icon :icon="['fas', 'play-circle']" class="mr-1" />
                        Stream
                      </div>
                      <div v-if="file?.transcode" class="bg-blue-500 text-white text-xs px-2 py-1 rounded-md font-medium shadow-sm">
                        <span v-for="(value, key, index) in file.transcode" :key="index">
                          <span v-if="typeof value === 'string' && value.includes('http')">{{ key }}</span>
                          <span v-else>{{ key }}: {{ value }}%</span>
                          <span v-if="index < Object.keys(file.transcode).length - 1">, </span>
                        </span>
                      </div>
                    </div>

                    <!-- Play overlay -->
                    <div class="absolute inset-0 bg-black bg-opacity-0 group-hover:bg-opacity-30 transition-all duration-200 flex items-center justify-center">
                      <font-awesome-icon :icon="['fas', 'play-circle']" class="text-white text-3xl opacity-0 group-hover:opacity-100 transition-opacity duration-200" />
                    </div>
                  </template>
                  <template v-else>
                    <div class="w-full h-full flex items-center justify-center bg-gray-200 relative">
                      <font-awesome-icon :icon="['fas', 'file-video']" class="text-gray-400 text-5xl" />
                      
                      <!-- Status badges for videos without thumbnails -->
                      <div class="absolute top-2 left-2 flex flex-col space-y-1">
                        <div v-if="file?.stream" class="bg-green-500 text-white text-xs px-2 py-1 rounded-md font-medium shadow-sm flex items-center">
                          <font-awesome-icon :icon="['fas', 'play-circle']" class="mr-1" />
                          Stream
                        </div>
                        <div v-if="file?.transcode" class="bg-blue-500 text-white text-xs px-2 py-1 rounded-md font-medium shadow-sm">
                          <span v-for="(value, key, index) in file.transcode" :key="index">
                            <span v-if="typeof value === 'string' && value.includes('http')">{{ key }}</span>
                            <span v-else>{{ key }}: {{ value }}%</span>
                            <span v-if="index < Object.keys(file.transcode).length - 1">, </span>
                          </span>
                        </div>
                      </div>
                    </div>
                  </template>
                </div>
                <button type="button" class="absolute inset-0 focus:outline-none">
                  <span class="sr-only">View details for {{file.name}}</span>
                </button>
              </div>
            
              <!-- File info section -->
              <div class="p-3">
                <div v-if="Mode === 'popup' && isAllowedFile(file.name)">
                  <label class="flex items-center space-x-2 cursor-pointer">
                    <input type="radio" :value="file" v-model="selectFile" class="w-4 h-4 text-blue-600 border-gray-300 focus:ring-blue-500" /> 
                    <span class="text-sm font-medium text-gray-900 truncate">{{ file.name }}</span>
                  </label>
                  <p class="text-xs text-gray-500 mt-1">{{ this.formatBytes(file.size) }}</p>
                  <!-- Select button for popup mode -->
                  <button 
                    @click.stop="selectFileFunc(file, file?.duration, file?.thumbnail)"
                    class="mt-2 w-full inline-flex items-center justify-center px-3 py-1.5 bg-blue-600 hover:bg-blue-700 text-white rounded-md text-xs font-medium transition-colors duration-200 shadow-sm"
                  >
                    <font-awesome-icon :icon="['fas', 'check']" class="mr-1 text-xs"/>
                    
                  </button>
                </div>

                <div v-else>
                  <p class="text-sm font-medium text-gray-900 truncate mb-1" :title="file.name">
                    <span class="text-gray-900">{{ getFileName(file.name) }}</span>
                    <span class="text-gray-500">{{ getFileExtension(file.name) }}</span>
                  </p>
                  <div class="flex justify-between items-center text-xs text-gray-500">
                    <span>{{ this.formatBytes(file.size) }}</span>
                    <span v-if="file.type=='media'">{{ formatDuration(file.duration) }}</span>
                  </div>
                  <!-- Select button for popup mode (non-allowed files show disabled) -->
                  <div v-if="Mode === 'popup'" class="mt-2">
                    <button 
                      disabled
                      class="w-full inline-flex items-center justify-center px-3 py-1.5 bg-gray-300 text-gray-500 rounded-md text-xs font-medium cursor-not-allowed"
                    >
                      <font-awesome-icon :icon="['fas', 'ban']" class="mr-1 text-xs"/>
                      
                    </button>
                  </div>
                </div>
              </div>
            </div>

          </div>

        </div>

        <!-- List Layout -->
        <div v-else class="bg-white rounded-xl shadow-sm border border-gray-200 overflow-hidden">
          <!-- Header -->
          <div class="bg-gradient-to-r from-gray-50 to-gray-100 px-6 py-3 border-b border-gray-200">
            <div :class="Mode === 'popup' ? 'grid grid-cols-13 gap-4' : 'grid grid-cols-12 gap-4'" class="text-xs font-semibold text-gray-600 uppercase tracking-wider">
              <div class="col-span-1 flex items-center">
                <font-awesome-icon :icon="['fas', 'image']" class="w-4 h-4"/>
              </div>
              <div :class="Mode === 'popup' ? 'col-span-4' : 'col-span-5'"></div>
              <div class="col-span-2"></div>
              <div class="col-span-2"></div>
              <div class="col-span-2"></div>
              <div v-if="Mode === 'popup'" class="col-span-2"></div>
            </div>
          </div>

          <!-- File List -->
          <ul role="list" class="divide-y divide-gray-100">
            <li v-for="(file) in displayedFiles" :key="file._id" 
                class="group hover:bg-blue-50 transition-colors duration-200 cursor-pointer"
                :class="{ 'bg-blue-100 border-l-4 border-blue-500': file._id === selectItem }"
                @click.stop="openViewFile(file.name)">
              
              <div v-if="file.mimetype === 'folder'"
                   @contextmenu.stop="onContextMenu($event, file.name, file.name, file._id, file.count)"
                   @dblclick.stop="openFolder(file.path)"
                   class="px-6 py-4">
                <div :class="Mode === 'popup' ? 'grid grid-cols-13 gap-4 items-center' : 'grid grid-cols-12 gap-4 items-center'">
                  <!-- Icon -->
                  <div class="col-span-1">
                    <div class="w-10 h-10 bg-gradient-to-br from-yellow-100 to-yellow-200 rounded-lg flex items-center justify-center relative">
                      <font-awesome-icon :icon="['fas', 'folder']" class="text-yellow-500 text-xl"/>
                      <div class="absolute -top-1 -right-1 bg-blue-500 text-white text-xs px-1.5 py-0.5 rounded-full font-medium shadow-sm min-w-[18px] text-center">
                        {{file.count}}
                      </div>
                    </div>
                  </div>
                  
                  <!-- Name -->
                  <div :class="Mode === 'popup' ? 'col-span-4' : 'col-span-5'">
                    <div class="flex items-center space-x-3">
                      <div>
                        <p class="text-sm font-semibold text-gray-900 group-hover:text-blue-700 transition-colors duration-200">
                          {{file.name}}
                        </p>
                        <p class="text-xs text-gray-500">{{file.count}} </p>
                      </div>
                    </div>
                  </div>
                  
                  <!-- Size -->
                  <div class="col-span-2">
                    <span class="text-sm text-gray-600 font-medium">{{ formatBytes(file.size) }}</span>
                  </div>
                  
                  <!-- Type -->
                  <div class="col-span-2">
                    <span class="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-yellow-100 text-yellow-800">
                      <font-awesome-icon :icon="['fas', 'folder']" class="w-3 h-3 mr-1"/>
                      
                    </span>
                  </div>
                  
                  <!-- Date -->
                  <div class="col-span-2">
                    <span class="text-sm text-gray-500">{{ formatDate(file.createdAt) }}</span>
                  </div>
                  
                  <!-- Action (Popup mode only) -->
                  <div v-if="Mode === 'popup'" class="col-span-2">
                    <button 
                      v-if="isAllowedFile(file.name)"
                      @click.stop="selectFileFunc(file, file?.duration, file?.thumbnail)"
                      class="inline-flex items-center px-2 py-1 bg-blue-600 hover:bg-blue-700 text-white rounded-md text-xs font-medium transition-colors duration-200"
                    >
                      <font-awesome-icon :icon="['fas', 'check']" class="mr-1 text-xs"/>
                      
                    </button>
                    <button 
                      v-else
                      disabled
                      class="inline-flex items-center px-2 py-1 bg-gray-200 text-gray-500 rounded-md text-xs font-medium cursor-not-allowed"
                    >
                      <font-awesome-icon :icon="['fas', 'ban']" class="mr-1 text-xs"/>
                      
                    </button>
                  </div>
                </div>
              </div>

              <div v-else
                   @contextmenu.stop="onFileContextMenu($event, file.name, file.name, file._id, file.original)"
                   @dblclick.stop="onDoubleClick(file.name)"
                   class="px-6 py-4">
                <div :class="Mode === 'popup' ? 'grid grid-cols-13 gap-4 items-center' : 'grid grid-cols-12 gap-4 items-center'">
                  <!-- Thumbnail/Icon -->
                  <div class="col-span-1">
                    <div class="w-10 h-10 rounded-lg overflow-hidden bg-gray-100 flex items-center justify-center">
                      <template v-if="file.type === 'image' && (file.thumbnail || file.path)">
                        <img :src="file.thumbnail ? file.thumbnail : file.path" 
                             alt="" 
                             class="w-full h-full object-cover">
                      </template>
                      <template v-else-if="file.type === 'media' && file.thumbnail">
                        <div class="relative w-full h-full">
                          <img :src="file.thumbnail" 
                               alt="" 
                               class="w-full h-full object-cover">
                          <div class="absolute inset-0 bg-black bg-opacity-30 flex items-center justify-center">
                            <font-awesome-icon :icon="['fas', 'play']" class="text-white text-xs"/>
                          </div>
                        </div>
                      </template>
                      <template v-else>
                        <font-awesome-icon 
                          :icon="['fas', getDocumentIcon(getNewFileExtension(file.name))]" 
                          :class="getDocumentIconColor(getNewFileExtension(file.name))"
                          class="text-lg"/>
                      </template>
                    </div>
                  </div>
                  
                  <!-- Name -->
                  <div :class="Mode === 'popup' ? 'col-span-4' : 'col-span-5'">
                    <div class="flex items-center space-x-3">
                      <div class="min-w-0 flex-1">
                        <p class="text-sm font-semibold text-gray-900 group-hover:text-blue-700 transition-colors duration-200 truncate">
                          <span>{{ getFileName(file.name) }}</span>
                          <span class="text-gray-500 font-normal">{{ getFileExtension(file.name) }}</span>
                        </p>
                        <div class="flex items-center space-x-2 mt-1">
                          <template v-if="file.stream">
                            <span class="inline-flex items-center px-1.5 py-0.5 rounded text-xs font-medium bg-green-100 text-green-800">
                              <font-awesome-icon :icon="['fas', 'play-circle']" class="w-3 h-3 mr-1"/>
                              Stream
                            </span>
                          </template>
                          <template v-if="file.transcode">
                            <span class="inline-flex items-center px-1.5 py-0.5 rounded text-xs font-medium bg-blue-100 text-blue-800">
                              Transcoded
                            </span>
                          </template>
                          <template v-if="file.type === 'media' && file.duration">
                            <span class="text-xs text-gray-500">{{ formatDuration(file.duration) }}</span>
                          </template>
                        </div>
                      </div>
                    </div>
                  </div>
                  
                  <!-- Size -->
                  <div class="col-span-2">
                    <span class="text-sm text-gray-600 font-medium">{{ formatBytes(file.size) }}</span>
                  </div>
                  
                  <!-- Type -->
                  <div class="col-span-2">
                    <span class="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium"
                          :class="{
                            'bg-blue-100 text-blue-800': file.type === 'image',
                            'bg-purple-100 text-purple-800': file.type === 'media',
                            'bg-gray-100 text-gray-800': file.type === 'document'
                          }">
                      <font-awesome-icon 
                        :icon="['fas', file.type === 'image' ? 'image' : file.type === 'media' ? 'video' : 'file-alt']" 
                        class="w-3 h-3 mr-1"/>
                      {{ file.type === 'image' ? '' : file.type === 'media' ? '' : '' }}
                    </span>
                  </div>
                  
                  <!-- Date -->
                  <div class="col-span-2">
                    <span class="text-sm text-gray-500">{{ formatDate(file.createdAt) }}</span>
                  </div>
                </div>
              </div>
            </li>
          </ul>
        </div>

        <!-- Empty State -->
        <div v-if="displayedFiles.length === 0" class="text-center py-12">
          <font-awesome-icon :icon="['fas', 'folder-open']" class="text-gray-300 text-4xl mb-4"/>
          <p class="text-gray-500"></p>
        </div>
      </div>
    </div>

    <!-- Popup Mode Footer -->
    <div v-if="Mode === 'popup'" class="bg-white border-t border-gray-200 px-4 py-3 flex-shrink-0">
      <div class="flex items-center justify-between">
        <div class="flex items-center space-x-3">
          <div v-if="selectFile" class="flex items-center space-x-2">
            <div class="w-8 h-8 bg-blue-100 rounded-lg flex items-center justify-center">
              <font-awesome-icon 
                :icon="['fas', selectFile.type === 'image' ? 'image' : selectFile.type === 'media' ? 'video' : 'file-alt']" 
                class="text-blue-600 text-sm"
              />
            </div>
            <div>
              <p class="text-sm font-medium text-gray-900 truncate max-w-xs" :title="selectFile.name">
                {{ selectFile.name }}
              </p>
              <p class="text-xs text-gray-500">{{ formatBytes(selectFile.size) }}</p>
            </div>
          </div>
          <div v-else class="text-sm text-gray-500">
            
          </div>
        </div>
        
        <div class="flex items-center space-x-3">
          <button 
            @click="$emit('close')"
            class="inline-flex items-center px-4 py-2 bg-gray-300 hover:bg-gray-400 text-gray-700 rounded-lg text-sm font-medium transition-colors duration-200"
          >
            
          </button>
          <button 
            @click="selectFileFunc(selectFile, selectFile?.duration, selectFile?.thumbnail)"
            :disabled="!selectFile"
            class="inline-flex items-center px-4 py-2 bg-blue-600 hover:bg-blue-700 disabled:bg-gray-300 disabled:cursor-not-allowed text-white rounded-lg text-sm font-medium transition-colors duration-200 shadow-sm"
          >
            <font-awesome-icon :icon="['fas', 'check']" class="mr-2 text-xs"/>
            
          </button>
        </div>
      </div>
    </div>
  </div>

  <!-- Mobile Sidebar Overlay -->
  <div 
    v-if="showMobileSidebar" 
    class="lg:hidden fixed inset-0 bg-black bg-opacity-50 z-50"
    @click="toggleMobileSidebar"
  >
    <div 
      class="w-64 h-full bg-white transform transition-transform duration-300"
      @click.stop
    >
      <!-- Mobile Sidebar Content (same as desktop sidebar) -->
      <div class="flex flex-col h-full">
        <!-- Sidebar Header -->
        <div class="flex items-center justify-between px-4 py-4 border-b border-gray-200">
          <h1 class="text-lg font-semibold text-gray-900">{{ PageName }}</h1>
          <button 
            @click="toggleMobileSidebar"
            class="p-2 text-gray-600 hover:text-gray-900 hover:bg-gray-100 rounded-lg transition-colors"
          >
            <i class="fas fa-times"></i>
          </button>
        </div>

        <!-- Mobile Quick Stats -->
        <div class="px-4 py-4 border-b border-gray-200">
          <h3 class="text-sm font-medium text-gray-700 mb-3"></h3>
          <div class="space-y-2">
            <div class="flex items-center justify-between p-2 bg-blue-50 rounded-lg">
              <div class="flex items-center gap-2">
                <div class="w-2 h-2 bg-blue-500 rounded-full"></div>
                <span class="text-sm text-gray-700"></span>
              </div>
              <span class="text-sm font-semibold text-blue-600">{{ fileListing.length }}</span>
            </div>
          </div>
        </div>

        <!-- Mobile Navigation Menu -->
        <div class="px-4 py-4 border-b border-gray-200">
          <h3 class="text-sm font-medium text-gray-700 mb-3"></h3>
          <nav class="space-y-1">
            <button 
              v-for="filterType in [
                { value: 'all', label: '', icon: 'fas fa-th-large' },
                { value: 'folder', label: '', icon: 'fas fa-folder' },
                { value: 'image', label: '', icon: 'fas fa-image' },
                { value: 'media', label: '', icon: 'fas fa-video' },
                { value: 'document', label: '', icon: 'fas fa-file-alt' }
              ]" 
              :key="filterType.value"
              @click="filterByType(filterType.value); toggleMobileSidebar()" 
              class="w-full flex items-center gap-3 px-3 py-2 text-sm rounded-lg transition-colors duration-200"
              :class="{ 
                'bg-blue-100 text-blue-700 font-medium': fileTypeFilter === filterType.value,
                'text-gray-600 hover:bg-gray-100 hover:text-gray-900': fileTypeFilter !== filterType.value
              }"
            >
              <i :class="filterType.icon" class="text-sm w-4"></i>
              <span class="flex-1 text-left">{{ filterType.label }}</span>
              <span 
                v-if="getFilterCount(filterType.value) > 0" 
                class="px-2 py-1 text-xs rounded-full"
                :class="{ 
                  'bg-blue-200 text-blue-800': fileTypeFilter === filterType.value,
                  'bg-gray-200 text-gray-600': fileTypeFilter !== filterType.value
                }"
              >
                {{ getFilterCount(filterType.value) }}
              </span>
            </button>
          </nav>
        </div>
      </div>
    </div>
  </div>
</div>

<!-- ... existing modals and components ... -->

<CreateFolder class="z-[99999]"
v-if="createFolderModal" 
:isWindowsOpen="true" 
@create-folder-trigger="createFolderTrigger" 
@create-folder-event="createFolder"/>

<RenameFolder class="z-[99999]"
v-if="RenameFolderModal" 
:isWindowsOpen="true" 
:renameTriggerObj="this.renameTriggerObj" 
:renameTriggerId="this.renameTriggerId"
:oldname="this.oldname" 
@rename-folder-trigger="RenameFolderTrigger" 
@rename-folder-event="RenameFolder"/>

<!-- Image Quality Modal -->
<div v-if="isResizeModalOpen" class="fixed inset-0 z-50 overflow-y-auto">
  <div class="fixed inset-0 bg-black/50 backdrop-blur-sm transition-opacity duration-300"></div>
  <div class="flex min-h-full items-center justify-center p-4">
    <div class="relative w-full max-w-md bg-white rounded-2xl shadow-2xl overflow-hidden transform transition-all duration-300">
      <!-- Header -->
      <div class="bg-gradient-to-r from-blue-600 to-indigo-600 px-6 py-4">
        <h2 class="text-xl font-semibold text-white"></h2>
        <p class="text-blue-100 text-sm mt-1"></p>
      </div>
      
      <!-- Content -->
      <div class="p-6">
        <!-- Quick select buttons -->
        <div class="mb-6">
          <label class="block text-sm font-medium text-gray-700 mb-3"></label>
          <div class="grid grid-cols-4 gap-2">
            <button 
              v-for="quality in [10, 30, 50, 80]" 
              :key="quality"
              @click="selectedImageQuality = quality/100"
              class="px-3 py-2 rounded-lg text-sm font-medium transition-all duration-200"
              :class="{
                'bg-blue-600 text-white shadow-md': selectedImageQuality === quality/100,
                'bg-gray-100 text-gray-700 hover:bg-gray-200': selectedImageQuality !== quality/100
              }">
              {{ quality }}%
            </button>
          </div>
        </div>

        <!-- Quality slider -->
        <div class="mb-6">
          <label for="quality" class="block text-sm font-medium text-gray-700 mb-3"></label>
          <div class="flex items-center space-x-4">
            <input 
              type="range" 
              v-model="selectedImageQuality" 
              id="quality" 
              min="0.1" 
              max="1" 
              step="0.01" 
              class="flex-1 h-2 bg-gray-200 rounded-lg appearance-none cursor-pointer slider"
            >
            <div class="min-w-[4rem] text-right">
              <span class="inline-flex items-center px-3 py-1 bg-blue-100 text-blue-800 text-sm font-medium rounded-full">
                {{ (selectedImageQuality * 100).toFixed(0) }}%
              </span>
            </div>
          </div>
        </div>
      </div>

      <!-- Footer -->
      <div class="bg-gray-50 px-6 py-4 flex justify-end space-x-3">
        <button 
          @click="isResizeModalOpen=false" 
          class="inline-flex items-center px-4 py-2 bg-gray-300 hover:bg-gray-400 text-gray-700 rounded-lg text-sm font-medium transition-colors duration-200">
          
        </button>
        <button 
          @click="handleResizeImage" 
          class="inline-flex items-center px-4 py-2 bg-blue-600 hover:bg-blue-700 text-white rounded-lg text-sm font-medium transition-colors duration-200 shadow-sm">
          <font-awesome-icon :icon="['fas', 'magic']" class="mr-2 text-xs"/>
          
        </button>
      </div>
    </div>
  </div>
</div>

<!-- Add Streaming File Modal -->
<div v-if="isModalVisible" class="fixed inset-0 z-50 overflow-y-auto">
  <div class="fixed inset-0 bg-black/50 backdrop-blur-sm transition-opacity duration-300"></div>
  <div class="flex min-h-full items-center justify-center p-4">
    <div class="relative w-full max-w-md bg-white rounded-2xl shadow-2xl overflow-hidden transform transition-all duration-300">
      <!-- Header -->
      <div class="bg-gradient-to-r from-green-600 to-emerald-600 px-6 py-4">
        <h3 class="text-xl font-semibold text-white"> Streaming File</h3>
        <p class="text-green-100 text-sm mt-1"> URL </p>
      </div>
      
      <!-- Content -->
      <div class="p-6 space-y-4">
        <!-- Name input field -->
        <div>
          <label for="name" class="block text-sm font-medium text-gray-700 mb-2"></label>
          <input
            v-model="fileName"
            type="text"
            id="name"
            placeholder=""
            class="w-full px-4 py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-green-500 focus:border-green-500 transition-colors duration-200"
          />
        </div>
        
        <!-- URL input field -->
        <div>
          <label for="url" class="block text-sm font-medium text-gray-700 mb-2">URL</label>
          <input
            v-model="fileUrl"
            type="url"
            id="url"
            placeholder=" URL "
            class="w-full px-4 py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-green-500 focus:border-green-500 transition-colors duration-200"
          />
        </div>
      </div>
      
      <!-- Footer -->
      <div class="bg-gray-50 px-6 py-4 flex justify-end space-x-3">
        <button
          @click="closeModal"
          class="inline-flex items-center px-4 py-2 bg-gray-300 hover:bg-gray-400 text-gray-700 rounded-lg text-sm font-medium transition-colors duration-200">
          
        </button>
        <button
          @click="submitFile"
          class="inline-flex items-center px-4 py-2 bg-green-600 hover:bg-green-700 text-white rounded-lg text-sm font-medium transition-colors duration-200 shadow-sm">
          <font-awesome-icon :icon="['fas', 'plus']" class="mr-2 text-xs"/>
          
        </button>
      </div>
    </div>
  </div>
</div>

<!-- File Preview Modal -->
<div v-if="showModal" :class="Mode === 'popup' ? 'fixed inset-0 z-[60]' : 'fixed inset-0 z-50'" class="overflow-y-auto">
  <!-- Backdrop with blur effect -->
  <div class="fixed inset-0 bg-black/80 backdrop-blur-sm transition-opacity duration-300"></div>
  
  <!-- Modal container -->
  <div class="flex min-h-full items-center justify-center p-2">
    <div class="relative w-full bg-white rounded-xl shadow-2xl overflow-hidden transform transition-all duration-300"
         :class="Mode === 'popup' ? 'max-w-3xl max-h-[50vh]' : 'max-w-5xl'"
         :style="Mode === 'popup' ? { maxHeight: '50vh' } : {}">
      
      <!-- Header -->
      <div class="relative bg-gradient-to-r from-gray-900 via-gray-800 to-gray-900 px-4 py-3">
        <div class="flex items-center justify-between">
          <!-- Left side - File info -->
          <div class="flex items-center space-x-3">
            <div class="flex items-center space-x-2">
              <div class="w-8 h-8 bg-white/10 rounded-lg flex items-center justify-center">
                <font-awesome-icon 
                  :icon="['fas', modalContent === 'image' ? 'image' : modalContent === 'video' ? 'video' : modalContent === 'pdf' ? 'file-pdf' : 'file']" 
                  class="text-white text-sm"
                />
              </div>
              <div>
                <h3 class="text-white font-semibold text-base truncate max-w-md" :title="viewFilename">
                  {{ viewFilename }}
                </h3>
                <p class="text-gray-300 text-xs">{{ formatBytes(viewFilesize) }}</p>
              </div>
            </div>
          </div>

          <!-- Right side - Action buttons -->
          <div class="flex items-center space-x-2">
            <!-- Convert options toggle -->
            <button 
              @click="showConvertOptions = !showConvertOptions" 
              type="button" 
              class="inline-flex items-center px-2 py-1.5 bg-green-600 hover:bg-green-700 text-white rounded-md text-xs font-medium transition-colors duration-200 shadow-sm">
              <font-awesome-icon :icon="['fas', 'cog']" class="mr-1 text-xs"/>
              {{ showConvertOptions ? '' : '' }}
            </button>

            <!-- Delete button -->
            <button 
              @click="deleteFile(viewFileurl)" 
              type="button" 
              class="inline-flex items-center px-2 py-1.5 bg-red-600 hover:bg-red-700 text-white rounded-md text-xs font-medium transition-colors duration-200 shadow-sm">
              <font-awesome-icon :icon="['fas','trash']" class="mr-1 text-xs"/>
              
            </button>

            <!-- Close button -->
            <button 
              @click="showModal = false" 
              type="button" 
              class="inline-flex items-center justify-center w-8 h-8 bg-gray-600 hover:bg-gray-700 text-white rounded-md transition-colors duration-200 shadow-sm">
              <font-awesome-icon :icon="['fas','times']" class="text-sm"/>
            </button>
          </div>
        </div>

        <!-- Convert options panel -->
        <div v-if="showConvertOptions" class="mt-3 p-3 bg-white/10 rounded-lg backdrop-blur-sm">
          <div class="flex items-center space-x-3">
            <div class="relative">
              <button 
                @click="toggleDropdown" 
                type="button" 
                class="flex items-center px-3 py-1.5 bg-indigo-600 hover:bg-indigo-700 text-white rounded-md shadow-sm transition-colors duration-200 text-xs font-medium">
                <span>{{ selectedQuality || '' }}</span>
                <font-awesome-icon :icon="['fas', 'chevron-down']" class="ml-2 text-xs"/>
              </button>
              
              <div v-if="dropdownOpen" class="absolute top-full left-0 mt-2 w-48 bg-white border border-gray-200 rounded-lg shadow-lg z-10 overflow-hidden">
                <ul class="py-1">
                  <li v-for="quality in filteredQualities" :key="quality">
                    <button 
                      @click="selectQuality(quality)" 
                      :disabled="selectItemData.transcode && selectItemData.transcode[quality]"
                      :class="{'opacity-50 cursor-not-allowed': selectItemData.transcode && selectItemData.transcode[quality]}"
                      class="block w-full text-left px-4 py-2 text-gray-700 hover:bg-gray-100 text-sm transition-colors duration-200">
                      {{ quality }} 
                      <span v-if="selectItemData.transcode && selectItemData.transcode[quality]" class="text-green-600 text-xs">
                        ()
                      </span>
                    </button>
                  </li>
                </ul>
              </div>
            </div>
            
            <button 
              v-if="selectedQuality" 
              @click="convertVideo(selectItemData)" 
              type="button" 
              class="inline-flex items-center px-3 py-1.5 bg-blue-600 hover:bg-blue-700 text-white rounded-md shadow-sm transition-colors duration-200 text-xs font-medium">
              <font-awesome-icon :icon="['fas','check']" class="mr-1 text-xs"/>
              
            </button>
          </div>
        </div>
      </div>
      
      <!-- Content area -->
      <div class="relative bg-gray-900 flex items-center justify-center"
           :class="Mode === 'popup' ? 'min-h-[200px] max-h-[30vh]' : 'min-h-[400px]'"
           :style="Mode === 'popup' ? { maxHeight: '30vh' } : {}">
        <!-- Image preview -->
        <template v-if="modalContent === 'image'">
          <div class="w-full h-full flex items-center justify-center p-3">
            <img 
              :src="selectedFile" 
              alt="Preview" 
              :class="Mode === 'popup' ? 'max-w-full max-h-[25vh] object-contain rounded-lg shadow-2xl' : 'max-w-full max-h-[60vh] object-contain rounded-lg shadow-2xl'"
            />
          </div>
        </template>
        
        <!-- Video preview -->
        <template v-else-if="modalContent === 'video'">
          <div class="w-full h-full flex items-center justify-center p-3">
            <!-- Streaming video -->
            <template v-if="selectItemData?.stream && selectItemData?.stream?.streamId">
              <div class="relative w-full max-w-4xl">
                <!-- Stream status badge -->
                <div class="absolute top-2 left-2 z-10">
                  <div class="inline-flex items-center px-2 py-1 bg-green-600 text-white text-xs font-medium rounded-full shadow-lg">
                    <div class="w-1.5 h-1.5 bg-green-300 rounded-full mr-1.5 animate-pulse"></div>
                    Stream: {{ selectItemData.stream.streamStatus }}
                  </div>
                </div>
                
                <VideoPlayer
                  type="default"
                  @pause="processPause"
                  :previewImageLink="selectItemData?.stream?.streamThumbnail"
                  :link="selectItemData?.stream?.streamPlayback"
                  :isMuted="false"
                  :isControls="true"
                  class="w-full h-full rounded-lg overflow-hidden shadow-2xl"
                />
              </div>
            </template>
            
            <!-- Regular video -->
            <template v-else>
              <div class="relative w-full max-w-4xl">
                <!-- Video type badge -->
                <div class="absolute top-2 left-2 z-10">
                  <div class="inline-flex items-center px-2 py-1 bg-blue-600 text-white text-xs font-medium rounded-full shadow-lg">
                    <font-awesome-icon :icon="['fas', 'play']" class="mr-1.5 text-xs"/>
                    Video
                  </div>
                </div>
                
                <video 
                  ref="videoStreaming" 
                  controls 
                  crossorigin 
                  playsinline
                  class="w-full h-full rounded-lg shadow-2xl"
                  :style="Mode === 'popup' ? 'max-height: 25vh;' : 'max-height: 60vh;'">
                  <source :src="selectedFile">
                </video>
              </div>
            </template>
          </div>
        </template>
        
        <!-- PDF preview -->
        <template v-else-if="modalContent === 'pdf'">
          <div class="w-full h-full flex items-center justify-center p-3">
            <div class="relative w-full max-w-5xl">
              <!-- PDF type badge -->
              <div class="absolute top-2 left-2 z-10">
                <div class="inline-flex items-center px-2 py-1 bg-red-600 text-white text-xs font-medium rounded-full shadow-lg">
                  <font-awesome-icon :icon="['fas', 'file-pdf']" class="mr-1.5 text-xs"/>
                  PDF Document
                </div>
              </div>
              
              <!-- PDF viewer using iframe -->
              <div class="w-full bg-white rounded-lg shadow-2xl overflow-hidden" 
                   :style="Mode === 'popup' ? 'height: 25vh;' : 'height: 60vh;'">
                <iframe 
                  :src="selectedFile + '#toolbar=1&navpanes=1&scrollbar=1&page=1&view=FitH'"
                  class="w-full h-full border-0"
                  type="application/pdf"
